#music-controls {
  position: fixed;
  left: -50px;
  top: 50%;
  transform: translateY(-50%);
  background-color: #ffffff73;
  color: #000000;
  padding: 10px;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: left 0.3s ease;
}

#music-controls.hidden {
  display: none;
}

#music-controls:hover {
  left: 0;
}

#music-controls .control-icon {
  background-color: #ffb7bf90;
  color: #fff;
  border: none;
  padding: 10px;
  cursor: pointer;
  margin-bottom: 10px;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

#music-controls .control-icon:hover {
  background-color: #ffb7c0;
}

/* 播放图标 */
#music-controls .control-icon.play::before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 15px solid #fff;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

/* 暂停图标 */
#music-controls .control-icon.pause::before,
#music-controls .control-icon.pause::after {
  content: '';
  position: absolute;
  width: 5px;
  height: 20px;
  background-color: #fff;
}

#music-controls .control-icon.pause::before {
  left: 10px;
}

#music-controls .control-icon.pause::after {
  left: 25px;
}

/* 静音图标 */
#music-controls .control-icon.mute {
  background-color: #ffb7c0;
  color: #fff;
  border: none;
  padding: 10px;
  cursor: pointer;
  margin-bottom: 10px;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

#music-controls .control-icon.mute::before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 10px solid #fff;
  left: 15px;
  top: 50%;
  transform: translate(-50%, -50%);
}

#music-controls .control-icon.mute::after {
  content: '';
  position: absolute;
  width: 10px;
  height: 2px;
  background-color: #fff;
  left: 15px;
  top: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
}

/* 取消静音图标 */
#music-controls .control-icon.unmute::before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 10px solid #fff;
  left: 15px;
  top: 50%;
  transform: translate(-50%, -50%);
}

#music-controls .control-icon.unmute::after {
  content: '';
  position: absolute;
  width: 10px;
  height: 2px;
  background-color: #fff;
  left: 15px;
  top: 50%;
  transform: translate(-50%, -50%);
  transform: rotate(45deg);
}

#music-controls .control-icon.unmute::after::before {
  content: '';
  position: absolute;
  width: 10px;
  height: 2px;
  background-color: #fff;
  left: 0;
  top: 0;
  transform: rotate(-45deg);
}

#music-controls input[type="range"] {
  width: 20px;
  height: 150px;
  appearance: slider-vertical; /* 移除默认样式 */
  background-color: transparent;
  transition: background-color 0.3s ease;
}

/* 滑动条样式 */
#music-controls input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 100%;
  background-color: #ffb7bfad; /* 滑动条颜色 */
  border-radius: 5px; /* 可选：圆角 */
}

/* 滑块样式 */
#music-controls input[type="range"]::-webkit-slider-thumb {
  appearance: slider-vertical;
  width: 10px; /* 滑块宽度 */
  height: 10px; /* 滑块高度 */
  background-color: #ffb7c0; /* 滑块颜色 */
  border-radius: 50%; /* 圆形滑块 */
  cursor: pointer;
  margin-top: 0; /* 移除可能影响拖动的margin-top */
}