
.navbar {
    font-family: 'DancingScript-SemiBold', sans-serif;
    font-size: 20px;
  
    position: relative;
    overflow: hidden;
    height: 100px;
    border-radius: 15px; // 添加圆角
    display: flex; // 使用 Flexbox 布局
    justify-content: space-around; // 水平居中并均匀分布
    align-items: center; // 垂直居中
  }
  
  .navbar::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #ffb7c0;
    z-index: 1;
    border-radius: 15px; // 确保背景层也有圆角
  }
  
  .navbar .nav-links {
    position: relative;
    z-index: 2;
    display: flex;
  }
  
  .navbar .nav-links a {
    color: white; // 设置链接颜色
    text-decoration: none; // 去掉下划线
    padding: 10px 20px; // 添加内边距
    border-radius: 15px; // 添加圆角
    transition: background-color 0.3s; // 添加过渡效果
  }
  
  .navbar .nav-links a:hover {
    border-radius: 15px; // 添加圆角
    background-color: rgba(255, 255, 255, 0.2); // 鼠标悬停时背景变亮
  }